<template>
  <div class="home">
    <div class="lottery">
      <LuckyWheel
        ref="myLucky"
        :width="width"
        :height="height"
        :prizes="prizes"
        :blocks="blocks"
        :buttons="buttons"
        @start="startCallback"
        @end="endCallback"
      />
    </div>
    <var-button color="#69dbaa" text-color="#eee" class="set" @click="setLucky"
      >设置</var-button
    >
    <var-action-sheet
      :actions="actions"
      :show.sync="show"
      @select="handleSelect"
    />
    <var-snackbar :show.sync="messageShow" type="success">
      {{ message }}</var-snackbar
    >
  </div>
</template>

<script>
export default {
  data() {
    return {
      blocks: [{ padding: "13px", background: "#617df2" }],
      prizes: [
        {
          fonts: [{ text: "很遗憾", top: "10%" }],
          background: "#e9e8fe",
        },
        {
          fonts: [{ text: "100元", top: "10%" }],
          background: "#b8c5f2",
        },
        {
          fonts: [{ text: "200元", top: "10%" }],
          background: "#e9e8fe",
        },
        {
          fonts: [{ text: "300元", top: "10%" }],
          background: "#b8c5f2",
        },
        {
          fonts: [{ text: "400元", top: "10%" }],
          background: "#e9e8fe",
        },
        {
          fonts: [{ text: "5000元", top: "10%" }],
          background: "#b8c5f2",
        },
      ],
      buttons: [
        {
          radius: "35%",
          background: "#8a9bf3",
          pointer: true,
          fonts: [{ text: "开始", top: "-10px" }],
        },
      ],
      width: "300px",
      height: "300px",
      probability: 0,
      show: false,
      actions: [
        {
          name: "不中奖",
          icon: "heart-outline",
          color: "black",
          index: 0,
        },
        {
          name: "100元",
          icon: "heart-outline",
          color: "#00c48f",
          index: 1,
        },
        {
          name: "200元",
          icon: "heart-outline",
          color: "green",
          index: 2,
        },
        {
          name: "300元",
          icon: "heart-outline",
          color: "#00afef",
          index: 3,
        },
        {
          name: "400元",
          icon: "heart-outline",
          color: "#ff9800",
          index: 4,
        },
        {
          name: "5000元",
          icon: "heart-outline",
          color: "red",
          index: 5,
        },
      ],
      messageShow: false,
      message: "",
      custom: false,
    };
  },
  methods: {
    // 点击抽奖按钮会触发star回调
    startCallback() {
      if (!this.custom) {
        this.probability = Math.floor(Math.random() * 6);
      }
      console.log(this.probability);
      // 调用抽奖组件的play方法开始游戏
      this.$refs.myLucky.play();
      this.$refs.myLucky.stop(this.probability);
    },
    endCallback(prize) {
      console.log(prize);
    },
    setLucky() {
      this.show = true;
    },
    // 选择事件
    handleSelect(val) {
      console.log(val);
      this.probability = val.index;
      this.show = false;
      this.message = `您已成功将奖品设置为${val.name}`;
      this.messageShow = true;
      this.custom = true;
    },
  },
  mounted() {},
};
</script>

<style scoped>
.home {
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
}
.lottery {
  width: 100%;
  display: flex;
  margin-top: 100px;
  justify-content: center;
}
.set {
  position: absolute;
  bottom: 0;
  right: 0;
}
</style>
